import screensConfig from './screens.json'

export default class Screens {
  constructor(todoist, pinboard, gcal) {
    this.todoist = todoist
    this.pinboard = pinboard
    this.gcal = gcal
  }

  screens() {
    let screenItems = []
    for (let screenItem of screensConfig) {
      switch (screenItem.type) {
        // handle grid screens
        case 'grid':
          let gridScreenItems = []
          for (let gridScreenItem of screenItem.items) {
            let newData = this.dataForGridScreenItem(gridScreenItem)
            let screenItem = Object.assign({}, gridScreenItem, newData)
            gridScreenItems.push(screenItem)
          }
          screenItem.items = gridScreenItems
          break
        case 'detail':
          break
      }
      screenItems.push(screenItem)
    }
    return screenItems
  }

  dataForGridScreenItem(gridScreenItem) {
    let newData = {data: ''}
    switch (gridScreenItem.dataSource) {
      case 'todoist':
        newData['data'] = this.todoist.dataForGridScreenItem(gridScreenItem)
        break
      case 'pinboard':
        newData['data'] = this.pinboard.unreadItems()
        break
      case 'gcal':
        newData['data'] = this.gcal.eventsThisMonth()
        break
      }
    return newData
  }
}



